.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;

    &-body {
        min-width: 300px;
        width: 500px;
        @media (max-width: 500px) {
            width: 100vw;
        }
        background-color: #fff;
        border: 1px solid black;
        border-radius: 4px;
    }
}